import React, { useEffect, useLayoutEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { ticketInformationAction } from '../../redux/Action/TicketAction'
import { DAT_GHE, XOA_GHE_DANG_CHON } from '../../util/Setting'
import { makeStyles } from '@material-ui/core/styles';
import EventSeatIcon from '@material-ui/icons/EventSeat';
import WeekendIcon from '@material-ui/icons/Weekend';
import { showMessageAlert } from '../../Template/SweetAlert'
import Lottie from 'react-lottie';
import * as location from '../../../src/loading.json'
export default function TicketList(props) {
  // console.log(props.maLichChieu)

  const { ticketInformation, danhSachGheDangChon } = useSelector(state => state.TicketReducer)
  console.log("danhSachGheDangChon", danhSachGheDangChon.length)
  const dispacth = useDispatch()
  console.log("ticketInformation", ticketInformation)

  useEffect(() => {
    dispacth(ticketInformationAction(props.maLichChieu))
  }, [])

  // Loading Page
  const defaultOptions = {
    loop: true,
    autoplay: true,
    animationData: location.default,
    rendererSettings: {
      preserveAspectRatio: "xMidYMid slice",
    },
  };
  const [data, setData] = useState([]);
  const [loading, setloading] = useState(undefined);
  const [completed, setcompleted] = useState(undefined);

  useLayoutEffect(() => {
    setTimeout(() => {
      fetch("https://jsonplaceholder.typicode.com/posts")
        .then((response) => response.json())
        .then((json) => {
          // console.log(json);
          setData(json);
          setloading(true);

          setTimeout(() => {
            setcompleted(true);
          }, 1000);
        });
    }, 1500);
  }, []);






  const renderDanhSachGhe = () => {
    return ticketInformation.danhSachGhe?.map((ghe, index) => {
      let ticketClass = "";
      let disabled = false;
      if (ghe.daDat === true) {
        ticketClass = 'gheDuocChon';
        disabled = true;
      }
      else if (danhSachGheDangChon?.findIndex(item => item === ghe) !== -1) {
        ticketClass = 'gheDangChon';
      }
      else if (ghe.loaiGhe === "Vip") {
        ticketClass = 'gheVip';
      }
      else {
        ticketClass = 'ghe';
      }
      return <div id="list__seat" style={{ display: "inline-block", flexGrow: 1, width: '6.25%' }}>
        <button
          onClick={() => {
            if (danhSachGheDangChon.length > 5) {
              showMessageAlert('Xin li', 'Bn vui lng khng chn qu 6 gh!! Hy chn li!!', 'warning')
              dispacth({
                type: XOA_GHE_DANG_CHON,
              })
            }
            else {
              orderChair(ghe)
            }
          }} className={`${ticketClass} `} disabled={disabled} ><WeekendIcon className="ghe__icon" />
        </button>
        <p className="tenGhe">{ghe.tenGhe}</p>
      </div>
    })
  }
  const orderChair = (thongTinGhe) => {
    dispacth({
      type: DAT_GHE,
      thongTinGhe: thongTinGhe
    })
  }
  return (

    <>
      {!completed ? (
        <>
          <div >
            {!loading ? (
              <Lottie options={defaultOptions} height={300} width={300} />
            ) : (
              <Lottie options={defaultOptions} height={300} width={300} />
            )}
          </div>
        </>
      )
        :
        (<div>
          <div className="container-fluid booking__title text-white ml-2">
            <div className="manHinh">
              <img src="../../img/screen.png" alt="manHinh" />
            </div>
            <div className="d-flex w-100 mt-3 ml-4" style={{ flexWrap: "wrap", }}>
              {renderDanhSachGhe()}
            </div>

            <div className="loai__ghe text-center row mt-3">
              <div className="col-3">
                <button className="ghe mr-2"><WeekendIcon style={{color: "#3e515d"}}/></button>
                <p className="tenGhe">Gh thng</p>
              </div>
              <div className="col-3">
                <button className="gheVip mr-2"><WeekendIcon style={{color: "#fb4226"}}/></button>
                <p className="tenGhe">Gh Vip</p>
              </div >
              <div className="col-3">
                <button className="gheDangChon mr-2"><WeekendIcon style={{color: "#44c020"}}/></button>
                <p className="tenGhe">Gh ang t</p>
              </div>
              <div className="col-3">
                <button className="gheDuocChon mr-2"><WeekendIcon style={{color: "#f7b500"}}/></button>
                <p className="tenGhe">Gh  t</p>
              </div>
            </div>
          </div>
        </div>)}
    </>
  )
}

