import React from 'react'
import { NavLink, Prompt } from 'react-router-dom'
import { useDispatch } from 'react-redux'
import { useFormik } from 'formik'
import * as Yup from 'yup'
import { loginAction } from '../../redux/Action/UsersAction'

export default function Login() {
    const dispatch = useDispatch()
    const formik = useFormik({
        initialValues: {
            taiKhoan: '',
            matKhau: '',
        },
        validationSchema: Yup.object().shape({
            taiKhoan: Yup.string().required('Ti khon khng c b trng'),
            matKhau: Yup.string().required('Mt khu khng c b trng').min(2, "Mt khu ti thiu 2 k t").max(32, "Mt khu ti a 32 k t")
        }),
        onSubmit: values => {
            const action = loginAction(values);
            dispatch(action)
            // console.log(values)
        }
    })

    return (
        <div id="tix__signin"  >
           
                <div className="signin__wrapper">
                    <div>
                        <form className="container" onSubmit={formik.handleSubmit}   >
                            <img className="loginLogo" src={require("../../assets/img/logo/loginLogo.png").default} alt="logoWeb" />
                          
                            
                            <div className="form-group">
                                <div className="input-group-prepend">
                                    <span className="input-group-text mr-1"> <i className='bx bx-user'></i> </span>
                                    <input className="form-control" placeholder="Ti khon" id="taiKhoan" type="text" onBlur={formik.handleBlur} onChange={formik.handleChange} />
                                </div>
                                {formik.errors.taiKhoan && formik.touched.taiKhoan ? <p className="text-danger">{formik.errors.taiKhoan}</p> : null}

                            </div>
                            <div className="form-group">
                                <div className="input-group-prepend">
                                    <span className="input-group-text mr-1"> <i class='bx bxs-lock-alt'></i> </span>
                                    <input className="form-control" placeholder="Mt khu" id="matKhau" type="password" onBlur={formik.handleBlur} onChange={formik.handleChange} />
                                </div>
                                {formik.errors.taiKhoan && formik.touched.matKhau ? <p className="text-danger">{formik.errors.matKhau}</p> : null}
                            </div>
                            <p className="text-center">Nu bn cha c ti khon,<NavLink className="text-danger" to="/register"> ng k ti y</NavLink></p>
                           
                            <div className="signin__footer">
                                <button type="submit" className="btn btn__signin" onClick={formik.handleSubmit}>ng nhp</button>
                            </div>
                        </form>
                    </div>
                </div>
           

        </div>
    )
}
