import React from 'react'
import { useFormik } from 'formik'
import * as Yup from 'yup'
import { useDispatch } from 'react-redux'
import { updateUserProfile } from '../../../redux/Action/AdminAction'
import { phoneRegex } from '../../../util/Setting'


export default function UpdateUser(props) {


    const dispatch = useDispatch()
    const formik = useFormik({
        initialValues: {
            taiKhoan: props.user.taiKhoan,
            matKhau: props.user.matKhau,
            hoTen: props.user.hoTen,
            soDt: props.user.soDt,
            maLoaiNguoiDung: props.user.maLoaiNguoiDung,
            maNhom: 'GP02',
            email: props.user.email
        },
        validationSchema: Yup.object().shape({
            taiKhoan: Yup.string().required('Ti khon khng c b trng'),
            matKhau: Yup.string().required('Ti khon khng c b trng').min(2, "Mt khu ti thiu 2 k t"),
            hoTen: Yup.string().required('H tn khng c b trng'),
            soDt: Yup.string().required('H tn khng c b trng'),
            maLoaiNguoiDung: Yup.string().required('M loi ngi dng khng c b trng'),
            email: Yup.string().required('Email khng c b trng'),
        }),
        enableReinitialize: true
    })

    const handleSubmit = () => {
        if (formik.isValid) {
            dispatch(updateUserProfile(formik.values))
        }
    }

    // useEffect(() => {
    //     console.log(formik.values)
    //     return formik.values
    // }, [formik.values])
    return (
        <div className="modal-content" style={{backgroundColor:"#323a46", color:"#dee2e6"}}>
            <div className="modal-header">
                <h5 className="modal-title" id="exampleModalLabel">Cp nht</h5>
                <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true"></span>
                </button>
            </div>
            <div className="modal-body">
                <form onSubmit={formik.handleSubmit} enableReinitialize={formik.enableReinitialize}  >
                    <div className="row">
                        <div className="col-md-6 mb-3">
                            <label htmlFor="taiKhoan">Ti khon</label>
                            <input style={{height:"35px", backgroundColor:"#282c31", border:"0px solid #434b56", borderRadius:"3px", color:"#dee2e6"}} type="text" className="form-control" id="taiKhoan" disabled value={formik.values?.taiKhoan} onChange={formik.handleChange} onBlur={formik.handleBlur} />
                            <p className="text-danger">{formik.errors.taiKhoan}</p>
                        </div>
                        <div className="col-md-6 mb-3">
                            <label htmlFor="matKhau">Mt khu</label>
                            <input style={{height:"35px", backgroundColor:"#434b56", border:"0px solid #434b56", borderRadius:"3px", color:"#dee2e6"}} type="text" className="form-control" id="matKhau" value={formik.values?.matKhau} onChange={formik.handleChange} onBlur={formik.handleBlur} />
                            <p className="text-danger">{formik.errors.matKhau}</p>
                        </div>
                        <div className="col-md-6 mb-3">
                            <label htmlFor="hoTen">H Tn</label>
                            <input style={{height:"35px", backgroundColor:"#434b56", border:"0px solid #434b56", borderRadius:"3px", color:"#dee2e6"}} type="text" className="form-control" id="hoTen" value={formik.values?.hoTen} onChange={formik.handleChange} onBlur={formik.handleBlur} />
                            <p className="text-danger">{formik.errors.hoTen}</p>

                        </div>
                        <div className="col-md-6 mb-3">
                            <label htmlFor="sdt">S in thoi</label>
                            <input style={{height:"35px", backgroundColor:"#434b56", border:"0px solid #434b56", borderRadius:"3px", color:"#dee2e6"}} type="text" className="form-control" id="soDt" value={formik.values?.soDt} onChange={formik.handleChange} onBlur={formik.handleBlur} />
                            <p className="text-danger">{formik.errors.soDt}</p>

                        </div>
                        <div className="col-md-6 mb-3">
                            <label htmlFor="maLoaiNguoiDung">M loi ngi dng </label>
                            <select
                            style={{height:"35px", backgroundColor:"#434b56", border:"0px solid #434b56", borderRadius:"3px", color:"#dee2e6"}}
                                name="maLoaiNguoiDung"
                                className="custom-select"
                                onChange={formik.handleChange}
                                onBlur={formik.handleBlur}
                            >
                                <option value="" hidden>Chn m</option>
                                <option value="KhachHang">KhachHang</option>
                                <option value="QuanTri">QuanTri</option>
                            </select>
                            <p className="text-danger">{formik.errors.maLoaiNguoiDung}</p>
                        </div>
                        <div className="col-md-6 mb-3">
                            <label htmlFor="maNhom">M nhm</label>
                            <input style={{height:"35px", backgroundColor:"#282c31", border:"0px solid #434b56", borderRadius:"3px", color:"#dee2e6"}} type="text" className="form-control" id="maNhom" value="GP09" disabled />
                            <p className="text-danger">{formik.errors.maNhom}</p>
                        </div>
                        <div className="col-md-6 mb-3">
                            <label htmlFor="email">Email</label>
                            <input 
                            style={{height:"35px", backgroundColor:"#434b56", border:"0px solid #434b56", borderRadius:"3px", color:"#dee2e6"}} 
                            type="email" className="form-control" 
                            id="email" 
                            value={formik.values?.email} 
                            onChange={formik.handleChange} 
                            onBlur={formik.handleBlur} />
                            <p className="text-danger">{formik.errors.email}</p>
                        </div>
                    </div>
                </form>
            </div>
            <div className="modal-footer">
                <button type="button" className="btn btn__add_calendar" data-dismiss="modal" onClick={handleSubmit}>Cp nht</button>
            </div>
        </div>
    )
}
