import axios from "axios"
import { ACCESS_TOKEN } from '../../util/Setting'
import {showMessageAlert, showMessageAlertEvent,showMessageDelete} from '../../Template/SweetAlert'

export const getCalendarMovieAction = (calendarMovie) => {
    return async (dispatch) => {
        try {
            const result = await axios({
                url: `https://movie0706.cybersoft.edu.vn/api/QuanLyDatVe/TaoLichChieu`,
                method: 'POST',
                data: calendarMovie,
                headers: { 'Authorization': 'Bearer ' + localStorage.getItem(ACCESS_TOKEN) }
            })
            showMessageAlertEvent("Thm lch chiu thnh cng", "","success")
            console.log('result', result.data);
          
        } catch (errors) {
            showMessageAlert("Thm lch chiu tht bi","","error")
            console.log('errors', errors.response?.data)
        }
    }
}