/* eslint-disable no-sequences */
import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { updateInformationUser, userProfileAction } from '../../redux/Action/UsersAction'
import * as Yup from 'yup'
import { useFormik } from 'formik'
import moment from 'moment';

export default function UserProfile() {
    const dispatch = useDispatch()
    const { user, userProfile } = useSelector(state => state.UsersReducer)
   
   
    const formik = useFormik({
        initialValues: {
            taiKhoan: user.taiKhoan,
            matKhau: '',
            hoTen: user.hoTen,
            soDT: user.soDT,
            maLoaiNguoiDung: user.maLoaiNguoiDung,
            maNhom: 'GP09',
            email: user.email
        },
        validationSchema: Yup.object().shape({
            taiKhoan: Yup.string().required('Ti khon khng c b trng'),
            matKhau: Yup.string().min(2, "Mt khu ti thiu 2 k t").max(32, "Mt khu ti a 32 k t"),
            hoTen: Yup.string().required('H tn khng c b trng'),
            soDt: Yup.string().required('S in thoi khng c b trng'),
            maLoaiNguoiDung: Yup.string().required('M loi ngi dng khng c b trng'),
            email: Yup.string().required('Email khng c b trng'),
        }),

    })
    // console.log(formik.values)
    const handleSubmit = () => {
        console.log(formik.isValid)
        // console.log(formik.values)
        // if (formik.isValid) {
            dispatch(updateInformationUser(formik.values))
        // }
    }
    useEffect(() => {
        dispatch(userProfileAction(user))
    }, [dispatch, user])


    
    const renderTableUser = () => {
        return userProfile?.thongTinDatVe?.map((item, index) => {
            return item.danhSachGhe.map((chair, index1) => {
                return <tr key={index, index1}>
                    <td>{item.tenPhim}</td>
                    <td>{moment(item.ngayDat).format("DD/MM/YYYY")}</td>
                    <td>{(item.thoiLuongPhim)} pht</td>
                    <td>{chair.tenHeThongRap} </td>
                    <td>{chair.tenGhe}</td>
                    <td>{item.giaVe} </td>
                </tr>
            })
        })
    }

    return (
      
            <div className="container bg-dark mt-5">
                <div className="row pt-5">
                    <form onSubmit={formik.handleSubmit} className="col-4">
                        <div className="row">
                            <div className="border-right">
                                <div className="" >
                                    {/* <h4 className="text-left">Thng tin c nhn</h4> */}
                                    <div className="d-flex justify-content-center align-items-center mb-3">
                                        <img src="./img/profile/avatar.png" className="img-fluid" width={150} height={200} />   
                                    </div>
                                    <h3 className="text-center">{userProfile.hoTen}</h3>
                                    <div className="row mt-3">
                                    <div className="col-6">
                                            <label className="title__profile">H tn</label>
                                            <input
                                             style={{height:"35px", backgroundColor:"#434b56", border:"0px solid #434b56", borderRadius:"3px", color:"#dee2e6"}} 
                                            name="hoTen" type="text" className="form-control" defaultValue={userProfile.hoTen} onChange={formik.handleChange} />
                                            <p className="text-danger">{formik.errors.hoTen}</p>
                                        </div>
                                        <div className="col-6">
                                            <label className="title__profile">Mt khu</label>
                                            <input 
                                             style={{height:"35px", backgroundColor:"#434b56", border:"0px solid #434b56", borderRadius:"3px", color:"#dee2e6"}} 
                                            name="matKhau" type="text" className="form-control" defaultValue={userProfile.matKhau} onChange={formik.handleChange} />
                                            <p className="text-danger">{formik.errors.matKhau}</p>
                                        </div>
                                       
                                        <div className="col-6">
                                            <label className="title__profile">S in thoi</label>
                                            <input 
                                             style={{height:"35px", backgroundColor:"#434b56", border:"0px solid #434b56", borderRadius:"3px", color:"#dee2e6"}} 
                                            name="soDT" type="text" className="form-control" defaultValue={userProfile.soDT} onChange={formik.handleChange} />
                                            <p className="text-danger">{formik.errors.soDT}</p>
                                        </div>

                                        <div className="col-6">
                                            <label className="title__profile">Email</label>
                                            <input 
                                             style={{height:"35px", backgroundColor:"#434b56", border:"0px solid #434b56", borderRadius:"3px", color:"#dee2e6"}} 
                                            name="email" type="text" className="form-control" defaultValue={userProfile.email} onChange={formik.handleChange} />
                                            <p className="text-danger">{formik.errors.email}</p>
                                        </div>

                                        
                                    </div>
                                    <div className="mb-3 text-center"><button className="btn btn__profile" type="button" onClick={handleSubmit}>Cp nht</button></div>
                                </div>
                            </div>
                        </div>
                    </form>

                    <div className="col-8">
                        <h3 style={{color:"#fff", fontWeight:"bold"}}>Lch s t v:</h3>
                        <table className="table">
                            <thead>
                                <tr>
                                    <th>Tn Phim</th>
                                    <th>Ngy t</th>
                                    <th>Thi lng</th>
                                    <th>Rp</th>
                                    <th>S gh</th>
                                    <th>Gi v</th>
                                    
                                </tr>
                            </thead>
                            <tbody>
                                {renderTableUser()}
                            </tbody>
                        </table>
                    </div>


                </div>

            </div>


       
    )
}
