import React from 'react';
import Slider from "react-slick";
import PlayVideoMovieShowTime from './PlayVideoMovieShowingTime';
import {useSelector } from 'react-redux'

export default function MovieShowtime(props) {

    const { allMovie } = useSelector(state => state.MovieReducer)
    console.log("allmovei",allMovie);
    console.log(allMovie)
    function SampleNextArrow(props) {
        const { onClick } = props;
        return (
          <span className="arrow-right-slick" onClick={onClick}>
            <i class="bx bxs-chevrons-right"></i>
          </span>
        );
      }
      function SamplePrevArrow(props) {
        const {  onClick } = props;
        return (
          <span className="arrow-left-slick" onClick={onClick}>
            <i class="bx bxs-chevrons-left"></i>
          </span>
        );
      }
    const settings = {
        className: "center",
        centerMode: true,
        infinite: true,
        centerPadding: "10px",
        slidesToShow: 4,
        nextArrow: <SampleNextArrow />,
        prevArrow: <SamplePrevArrow />,
        speed: 500,
        rows: 2,
        slidesPerRow: 1,
        responsive: [
            {
                breakpoint: 1920,
                settings: {
                    slidesToShow: 4,
                    slidesPerRow: 1,
                    centerPadding: "10px",
                    rows: 2,
                }
            },
            {
                breakpoint: 1025,
                settings: {
                    slidesToShow: 3,
                    slidesPerRow: 1,
                    centerPadding: "10px",
                    rows: 2,
                }
            },
            
            {
                breakpoint: 769,
                settings: {
                    slidesToShow: 2,
                    slidesPerRow: 1,
                    centerPadding: "0px",
                    rows:2,
                   
                }
            },
            {
                breakpoint: 420,
                settings: {
                    slidesToShow: 1,
                    slidesPerRow: 1,
                    centerPadding: "0px",
                    rows:1,
                   
                }
            }
        ]
    };
    

    const renderPlayVideoMovieShowTime = () => {
        return allMovie.filter(item=>item.maPhim % 3 !== 0).map((movie, index) => {
            return <div className="d-block" key={index}>
                <PlayVideoMovieShowTime movie={movie} />
            </div>

        })
    }

    const renderComingMovie = () =>{
        return allMovie.filter(item=>item.maPhim % 2 !== 0).map((movie, index) => {
            return <div className="d-block" key={index}>
                <PlayVideoMovieShowTime movie={movie} />
            </div>

        })
    }
    return (    
        <div>
            <div id="movie__schedule">
                <ul class="nav nav-pills mb-3 justify-content-center mt-5" id="pills-tab" role="tablist">
                    <li class="nav-item">
                        <a class="nav-link tabPanel active" id="pills-home-tab" data-toggle="pill" href="#showing" role="tab" aria-controls="pills-home" aria-selected="true">ang chiu</a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link tabPanel " id="pills-profile-tab" data-toggle="pill" href="#comingsoon" role="tab" aria-controls="pills-profile" aria-selected="false">Sp chiu</a>
                    </li>
                </ul>
                <div class="tab-content" id="pills-tabContent">
                    <div class="tab-pane fade show active" id="showing" role="tabpanel" aria-labelledby="pills-home-tab">
                        <div className="container movieSlider">
                            <Slider {...settings}>
                                {renderPlayVideoMovieShowTime()}
                            </Slider>

                        </div>
                    </div>
                    <div class="tab-pane fade" id="comingsoon" role="tabpanel" aria-labelledby="pills-profile-tab">
                        <div className="container movieSlider" >
                            <Slider {...settings}>
                                {renderComingMovie()}
                            </Slider>

                        </div>
                    </div>

                </div>

            </div>
        </div>
    )
}
