import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { history } from '../../App'
import { orderTicket, ticketInformationAction } from '../../redux/Action/TicketAction'
import { DAT_GHE, XOA_GHE_DANG_CHON } from '../../util/Setting'
import Lottie from 'react-lottie';
import * as location from '../../../src/loading.json'
import {showMessageAlert} from '../../Template/SweetAlert'

export default function TicketInformation(props) {

    const { danhSachGheDangChon } = useSelector(state => state.TicketReducer)
    const { taiKhoan } = useSelector(state => state.UsersReducer)
    const [info, setInfo] = useState(
        {
            thongTinPhim: {
                diaChi: "",
                gioChieu: "",
                hinhAnh: "",
                maLichChieu: "",
                ngayChieu: "",
                tenCumRap: "",
                tenPhim: "",
                tenRap: "",
            }
        }
    )

    const getInfo = async (maLichChieu) => {
        try {
            const result = await axios({
                url: `https://movie0706.cybersoft.edu.vn/api/QuanLyDatVe/LayDanhSachPhongVe?MaLichChieu=${maLichChieu}`,
                method: "GET",
            });
            setInfo(result.data);
        } catch (errors) {
            console.log("errors", errors.response?.data);
        }
    };

    const dispacth = useDispatch()
    useEffect(() => {
        getInfo(props.maLichChieu)
    }, [])
    console.log("info", info.thongTinPhim);

    let ticketOrderList = danhSachGheDangChon.map((item, index) => {
        return { maGhe: item.maGhe, giaVe: item.giaVe }
    })

    const renderTicketInfomation = () => {
        return danhSachGheDangChon?.map((item, index) => {
            return <tr key={index} className="">
                <td >{item.tenGhe}  </td>
                <td >{item.loaiGhe} </td>
                <td >{item.giaVe} </td>
                <td><button className="btn btn__delete" onClick={() => dispacth({
                    type: DAT_GHE,
                    thongTinGhe: item
                })}><i class='bx bx-x'></i></button></td>
            </tr>
        })
    }

   
    const defaultOptions = {
        loop: true,
        autoplay: true,
        animationData: location.default,
        rendererSettings: {
            preserveAspectRatio: "xMidYMid slice",
        },
    };
  
    const [data, setData] = useState([]);
    const [loading, setloading] = useState(undefined);
    const [completed, setcompleted] = useState(undefined);

    useEffect(() => {
        setTimeout(() => {
            fetch("https://jsonplaceholder.typicode.com/posts")
                .then((response) => response.json())
                .then((json) => {
                    // console.log(json);
                    setData(json);
                    setloading(true);

                    setTimeout(() => {
                        setcompleted(true);
                    }, 1000);
                });
        }, 1500);
    }, []);

    return (
        <>
            {!completed ? (
                <>
                    <div>
                        {!loading ? (
                            <Lottie options={defaultOptions} height={300} width={300} />
                        ) : (
                            <Lottie options={defaultOptions} height={300} width={300} />
                        )}
                    </div>
                </>
            )
                : (
                    <div>
                        <div className="info__title ml-5">
                            <div className="info__film">
                                <h5>{info.thongTinPhim.tenPhim}</h5>
                                <div className="address">
                                    <p>{info.thongTinPhim.diaChi}  </p>
                                    <p><span>{info.thongTinPhim.ngayChieu}</span> <span>-</span>  <span>{info.thongTinPhim.gioChieu}</span> <span>-</span>   <span>{info.thongTinPhim.tenRap}</span></p>
                                </div>
                            </div>

                            <table className="table">
                                <thead>
                                    <tr className="text-white text-light">
                                        <th>Gh</th>
                                        <th>Loi</th>
                                        <th>Gi</th>
                                        {danhSachGheDangChon.toString() !== "" ? <th><button className="btn btn__delete__all" onClick={() => dispacth({
                                            type: XOA_GHE_DANG_CHON,
                                        })}><i class='bx bx-trash' ></i></button></th> : ""}
                                    </tr>
                                </thead>
                                <tbody>
                                    {renderTicketInfomation()}
                                </tbody>
                                <tfoot >
                                    {danhSachGheDangChon.toString() !== "" ? <tr className="text-white text-light">
                                        <th style={{fontSize:"20px"}}>Tng cng</th>
                                        <th>{`${danhSachGheDangChon.length} v`}</th>
                                        <th>{danhSachGheDangChon.reduce((tong, item, index) => {
                                            return tong += item.giaVe
                                        }, 0).toLocaleString()} VN</th>
                                        {taiKhoan === '' ? <th><button className="btn btn-success" onClick={() => {
                                            showMessageAlert('Xin li', 'Vui lng ng nhp  t v!!', 'error')
                                            history.push('/login')
                                        }}>t v</button></th> : <th><button className="btn btn__booking" onClick={() => {
                                            dispacth(orderTicket({
                                                maLichChieu: props.maLichChieu,
                                                danhSachVe: ticketOrderList,
                                                taiKhoanNguoiDung: taiKhoan

                                            }))
                                        }}>t v</button></th>}

                                    </tr> : ''}
                                </tfoot>
                            </table>
                        </div>
                    </div>
                )}
        </>

    )
}
