import React, { useEffect, useState } from 'react'
import { NavLink } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
import { getAllMovieAction, getDetailMovieAction } from '../../../redux/Action/MovieAction'
import { Autocomplete } from '@material-ui/lab'
import { TextField } from '@material-ui/core'
import { getCinemaAction, getLogoCinemaAction } from '../../../redux/Action/CinemaAction'
import { formatDate } from '../../../util/GetDateFormatted'
import { getCalendarMovieAction } from '../../../redux/Action/CalendarAction'

export default function CalendarMovieManagement() {
    const { allMovie} = useSelector(state => state.MovieReducer)
    const { allCinema, allCinemaLogo } = useSelector(state => state.CinemaReducer)
    const [maPhim, setMaPhim] = useState('')
    const [mangMaRap, setMangMaRap] = useState('')
    const [ngayChieu, setNgayChieu] = useState('')
    const [gioChieu, setGioChieu] = useState('')
    const [giaVe, setGiaVe] = useState('')
    const [maRap, setMaRap] = useState('')
    const [tenRap, setTenRap] = useState('')
    const dispatch = useDispatch()
    useEffect(() => {
        dispatch(getAllMovieAction())
        dispatch(getLogoCinemaAction())
        dispatch(getDetailMovieAction(maPhim))
        dispatch(getCinemaAction(tenRap))
    }, [dispatch, maPhim, tenRap])
    return (
        <div className="calendar__manager">
            <div>
                <div className="container-fluid mt-5">
                    <div className="row">
                    <nav className="col-md-2 col-lg-2 d-md-block bg-dark sidebar__menu sidebar ">
                            <div className="sidebar-sticky pt-3">
                                <ul className="nav flex-column mt-5">
                                    <li className="nav-item">
                                        
                                        <NavLink className="nav-link active" to='/admin/UsersManagement' >
                                        <i class='bx bxs-user-account'></i> Qun l ngi dng <span className="sr-only">(current)</span>
                                        </NavLink>
                                    </li>
                                    <li className="nav-item">
                                        <NavLink className="nav-link" to="/admin/MovieManagement">
                                        <i class='bx bx-film' ></i> Qun l phim
                                        </NavLink>
                                    </li>
                                    <li className="nav-item">
                                        <NavLink className="nav-link nav__active" to="/admin/CalendarMovieManagement">
                                        <i class='bx bx-calendar-star' ></i> Qun l lch chiu
                                        </NavLink>
                                    </li>

                                </ul>
                            </div>
                        </nav>
                        <main role="main" className="col-md-9 ml-sm-auto col-lg-10 mt-5">
                        <h4 className="text-center title__user__manager">Qun l lch chiu</h4>
                            <div className="row">
                                <div className="col-md-12">
                                    <div className="card" style={{backgroundColor:"#323a46"}}>
                                        <div className="card-body" style={{color:"#adb5bd"}}>
                                            <form >
                                                <div className="row">
                                                    <div className="col-md-6 mb-3">
                                                        <label htmlFor="maPhim">Tn phim</label>
                                                        <Autocomplete
                                                            style={{height:"56px", backgroundColor:"#434b56", borderRadius:"3px", color:"#dee2e6"}}
                                                            id="combo-box-demo"
                                                            options={allMovie}
                                                            name="maPhim"
                                                            getOptionLabel={(option) => option?.tenPhim}
                                                            onChange={(e, value) => {
                                                                setMaPhim(value?.maPhim)
                                                       
                                                            }}
                                                            disableClearable={true}

                                                            renderInput={(params) => <TextField {...params} label="Tn phim" variant="outlined" />}
                                                        />


                                                    </div>
                                                    <div className="col-md-6 mb-3">
                                                        <label htmlFor="ngayChieuGioChieu">Ngy chiu </label>
                                                        <input style={{height:"56px", backgroundColor:"#434b56", border:"0px solid #434b56", borderRadius:"3px", color:"#dee2e6"}} type="date" className="form-control" id="ngayChieuGioChieuValue" placeholder="" onChange={(e) => {
                                                            setNgayChieu(e.target?.value)
                                                        }} />
                                                    </div>

                                                    <div className="col-md-6 mb-3">
                                                        <label htmlFor="maRap">Tn rp</label>
                                                        <Autocomplete
                                                            
                                                            style={{height:"56px", backgroundColor:"#434b56", borderRadius:"3px", color:"#dee2e6"}}
                                                            id="combo-box-demo"
                                                            options={allCinemaLogo}
                                                            getOptionLabel={(option) => option?.maHeThongRap}
                                                            onChange={(e, value) => {
                                                                setTenRap(value?.maHeThongRap)
                                                    
                                                            }}
                                                            disableClearable={true}
                                                            renderInput={(params) => <TextField {...params} label="Tn rp" variant="outlined" />}
                                                        />

                                                    </div>

                                                    <div className="col-md-6 mb-3">
                                                        <label htmlFor="ngayChieuGioChieu">Gi chiu</label>
                                                        <input style={{height:"56px", backgroundColor:"#434b56", border:"0px solid #434b56", borderRadius:"3px", color:"#dee2e6"}} type="time" step="1" className="form-control" id="ngayChieuGioChieuValue" placeholder="" onChange={(e) => {
                                                            setGioChieu(e.target?.value)
                                                        }} />
                                                    </div>

                                                    <div className="col-md-6 mb-3">
                                                        <label htmlFor="maRap">a ch rp</label>
                                                        <Autocomplete
                                                             style={{height:"56px", backgroundColor:"#434b56", borderRadius:"3px", color:"#dee2e6"}}
                                                            disabled={maPhim === '' || tenRap === ''}
                                                            id="combo-box-demo"
                                                            options={allCinema}
                                                       
                                                            getOptionLabel={(option) => option?.diaChi}
                                                            onChange={(e, value) => {
                                                                setMangMaRap(value?.danhSachRap)
                                                            }}
                                                            disableClearable={true}

                                                            renderInput={(params) => <TextField {...params} label="a ch rp" variant="outlined" />}
                                                        />

                                                    </div>
                                                    <div className="col-md-6 mb-3">
                                                        <label htmlFor="giaVe">Gi v</label>
                                                        <input style={{height:"56px", backgroundColor:"#434b56", border:"0px solid #434b56", borderRadius:"3px", color:"#dee2e6"}} type="text" className="form-control" id="giaVe" placeholder="" required onChange={(e) => {
                                                            setGiaVe(e.target?.value)
                                                        }} />

                                                    </div>
                                                    <div className="col-md-6 mb-3">
                                                        <label htmlFor="maRap">M rp</label>
                                                        <Autocomplete
                                                         style={{height:"56px", backgroundColor:"#434b56", borderRadius:"3px", color:"#dee2e6"}}
                                                            disabled={maPhim === '' || tenRap === '' || mangMaRap === ''}

                                                            id="combo-box-demo"
                                                            options={mangMaRap}
                                                            getOptionLabel={(option) => option?.maRap.toString()}
                                                            onChange={(e, value) => {
                                                                setMaRap(value?.maRap)

                                                            }}
                                                            disableClearable={true}

                                                            renderInput={(params) => <TextField {...params} label="M rp" variant="outlined" />}
                                                        />

                                                    </div>
                                                </div>



                        
                                                <button className="btn btn__add_calendar" type="button" onClick={() => {
                                                    dispatch(getCalendarMovieAction({
                                                        maPhim,
                                                        ngayChieuGioChieu: formatDate(ngayChieu) + " " + gioChieu,
                                                        maRap,
                                                        giaVe
                                                    }))
                                                }}>Thm lch chiu</button>
                                            </form>
                                        </div>
                                    </div>
                                </div>

                            </div>
                        </main>
                    </div>
                </div>

            </div>
        </div >
    )
}
