/* eslint-disable jsx-a11y/alt-text */
import React, { useEffect, useState } from "react";
import axios from "axios";
import {NavLink, Link } from "react-router-dom";
import moment from 'moment';
export default function Cinema() {
  const [allCinemaLogo, setAllCinemaLogo] = useState([]);
  const [allCinemaSchedule, setallCinemaSchedule] = useState("");
  const [cinemaLogoId, setCinemaLogoId] = useState("");
  const [cinemaId, setCinemaId] = useState("");

  const getCinemaLogo = async () => {
    try {
      const result = await axios({
        url: "https://movie0706.cybersoft.edu.vn/api/QuanLyRap/LayThongTinHeThongRap",
        method: "GET",
      });
      setAllCinemaLogo(result.data);
      setCinemaLogoId(result.data[0]?.maHeThongRap);
    } catch (errors) {
      console.log("errors", errors.response?.data);
    }
  };

  const getCinemaSchedule = async (ma) => {
    try {
      const result = await axios({
        url: `https://movie0706.cybersoft.edu.vn/api/QuanLyRap/LayThongTinLichChieuHeThongRap?maHeThongRap=${ma}&maNhom=GP09`,
        method: "GET",
      });
      setallCinemaSchedule(result.data);
      setCinemaId(result.data[0]?.lstCumRap[0].maCumRap);
    } catch (errors) {
      console.log("errors", errors.response?.data);
    }
  };

  useEffect(() => {
    getCinemaLogo();
    getCinemaSchedule(cinemaLogoId);
  }, []);

  const renderLogoCinema = () => {
    return allCinemaLogo?.map((cinema, index) => {
      return (
        <li className="nav-item active" key={index}>
          <a
            className="nav-link"
            onClick={() => {
              getCinemaSchedule(cinema.maHeThongRap);
            }}
          >
            <img src={cinema.logo} alt={cinema.maHeThongRap} />
          </a>
        </li>
      );
    });
  };
  const renderCinema = () => {
    return allCinemaSchedule[0]?.lstCumRap.map((cinema, index) => {
      return (
        <div
          className="cinemas active"
          key={index}
          onClick={() => {
            setCinemaId(cinema.maCumRap);
          }}
        >
          <img
            className="img__cinema"
            src={`./img/Cinema/${cinema.maCumRap}.png`} 
            alt=""
          />
          <div className="info__item">
            <span className="cinema__name">
              <span className="cinema__color"></span>
              {cinema.tenCumRap}
            </span>
            <span className="info__movie">{cinema.diaChi}</span>
            <span className="movie__detail">
              <a href="#">[chi tit]</a>
            </span>
          </div>
        </div>
      );
    });
  };
  const renderCinemaSchedule = () => {
    let danhSachPhim = allCinemaSchedule[0]?.lstCumRap.find(
      (item) => item.maCumRap === cinemaId
    );
    console.log("danhSachPhim", danhSachPhim)
    return danhSachPhim?.danhSachPhim.map((item, index) => {
      return (
        <div className="movie__item " key={index}>
          <Link
            to="/" style={{ textDecoration: "none" }}>
            <div className="movie__time">
              <img src={item.hinhAnh} className="img__movie" alt="" />
              <div className="info__item">
                <p>
                  
                  <span className="movie__title">{item.tenPhim}</span>
                </p>
                <p>{item.maPhim % 2 == 0 ? "115 pht - TIX 9.5 - IMDb 0" : "120 pht - TIX 9.7 - IMDb 0"} </p>
              </div>
            </div>
            <div className="movie__hours collapse in">
              <div className="list__time">
                <div className="type__theater">2D Digital</div>
                <div className="timeSession">

                  {item.lstLichChieuTheoPhim.filter(item => item.maLichChieu % 2 !== 0).map((lctp, i) => {
                    return (<button className="btn__hours" >
                      <NavLink to={'/sellticket/' + lctp.maLichChieu}>{moment(lctp.ngayChieuGioChieu).format("HH:mm")}</NavLink></button>
                    )
                  })}
                </div>
              </div>
            </div>
          </Link>
        </div>
      );
    });
  };

  return (

    <section className="container" id="cinema">
      <div id="bg__top">
        <img src="./img/Movie/back-news.png" alt />
      </div>
      <div id="cinema__Content" className="row session-main">
        <ul className="col-md-1 nav nav-tabs list__logo">
          {renderLogoCinema()}
        </ul>
        <div
          id="address__cinema"
          className="col-md-4 bg-white"
          style={{ overflowY: "scroll" }}
        >
          {renderCinema()}
        </div>
        <div id="list__movie" style={{ overflowY: "scroll" }} className="col-md-7 bg-white">
          {renderCinemaSchedule()}
        </div>
      </div>
    </section>

  );
}
