import axios from 'axios'
import { GET_ALL_CINEMA, GET_ALL_CINEMA_LOGO, GET_ALL_CINEMA_SCHEDULE } from '../../util/Setting'

export const getLogoCinemaAction = () => {
    return async (dispatch) => {
        try {
            const result = await axios({
                url: 'https://movie0706.cybersoft.edu.vn/api/QuanLyRap/LayThongTinHeThongRap',
                method: 'GET'
            })
            dispatch({
                type: GET_ALL_CINEMA_LOGO,
                allCinemaLogo: result.data
            })
        } catch (errors) {
            console.log('errors', errors.response?.data)
        }
    }
}
export const getCinemaAction = (ma) => {
  return async (dispatch) => {
    try {
      const result = await axios({
        url: `https://movie0706.cybersoft.edu.vn/api/QuanLyRap/LayThongTinCumRapTheoHeThong?maHeThongRap=${ma}`,
        method: "GET",
      });
      dispatch({
        type: GET_ALL_CINEMA,
        allCinema: result.data,
      });
    } catch (errors) {
      console.log("errors", errors.response?.data);
    }
  };
};
export const getCinemaScheduleAction = (ma) => {
  return async (dispatch) => {
    try {
      const result = await axios({
        url: `https://movie0706.cybersoft.edu.vn/api/QuanLyRap/LayThongTinLichChieuHeThongRap?maHeThongRap=${ma}&maNhom=GP09`,
        method: "GET",
      });
      dispatch({
        type: GET_ALL_CINEMA_SCHEDULE,
        allCinemaSchedule: result.data,
      });
    } catch (errors) {
      console.log("errors", errors.response?.data);
    }
  };
};