import React, { useEffect, useState } from "react";
import Carousel from '../Carousel/Carousel'
import MovieShowtime from '../MovieShowtime/MovieShowtime'
import Cinema from '../Cinema/Cinema'
import News from '../News/News'
import Application from '../Application/Application'
import Lottie from 'react-lottie';
import * as location from '../../loadFilm.json'
import * as success from '../../success.json'



export default function Home() {

  // Loading Page
  const defaultOptions1 = {
    loop: true,
    autoplay: true,
    animationData: location.default,
    rendererSettings: {
      preserveAspectRatio: "xMidYMid slice",
    },
  };
  const defaultOptions2 = {
    loop: true,
    autoplay: true,
    animationData: success.default,
    rendererSettings: {
      preserveAspectRatio: "xMidYMid slice",
    },
  };
  const [data, setData] = useState([]);
  const [loading, setloading] = useState(undefined);
  const [completed, setcompleted] = useState(undefined);

  useEffect(() => {
    setTimeout(() => {
      fetch("https://jsonplaceholder.typicode.com/posts")
        .then((response) => response.json())
        .then((json) => {
          // console.log(json);
          setData(json);
          setloading(true);

          setTimeout(() => {
            setcompleted(true);
          }, 1000);
        });
    }, 1500);
  }, []);
  return (
    <>
      {!completed ? (
        <>
          <div style={{ marginTop: 50 }}>
            {!loading ? (
              <Lottie options={defaultOptions1} height={300} width={300} />
            ) : (
              <Lottie options={defaultOptions2} height={300} width={300} />
            )}
          </div>
        </>
      )
        : (
          <div>
            <Carousel />
            <MovieShowtime />
            <Cinema />
            <News />
            <Application />
            {/* <Footer /> */}
          </div>
        )}
    </>
  );
}