import { useFormik } from 'formik'
import React from 'react'
import * as Yup from 'yup'
import { useDispatch } from 'react-redux'
import { addUser } from '../../../redux/Action/AdminAction'
import { phoneRegex } from '../../../util/Setting'

export default function AddUser() {
    const dispatch = useDispatch()

    const formik = useFormik({
        initialValues: {
            taiKhoan: '',
            matKhau: 1,
            hoTen: 2,
            soDt: 3,
            maLoaiNguoiDung: 5,
            maNhom: 'GP09',
            email: 4,
        },
        validationSchema: Yup.object().shape({
            taiKhoan: Yup.string().required('Ti khon khng c b trng'),
            matKhau: Yup.string().required('Mt khu khng c b trng'),
            hoTen: Yup.string().required('H tn khng c b trng'),
            soDt: Yup.string().required('S in thoi khng c b trng'),
            maLoaiNguoiDung: Yup.string().required('M loi ngi dng khng c b trng'),
            email: Yup.string().required('Email khng c b trng'),
        }),
    })

    const handleSubmit = () => {
        // console.log("isValid", formik.isValid)
        if (formik.isValid) {
            // console.log(formik.values)
            dispatch(addUser(formik.values))
        }
    }

    return (
        <div className="modal-content" style={{ backgroundColor: "#323a46", color: "#dee2e6" }}>
            <div className="modal-header">
                <h5 className="modal-title" id="exampleModalLabel">Thm thnh vin</h5>
            </div>
            <div className="modal-body">
            
                <form onSubmit={formik.handleSubmit}>
                    <div className="row">
                        <div className="col-md-6 mb-3">
                            <label htmlFor="taiKhoan">Ti khon</label>
                            <input
                                style={{ height: "35px", backgroundColor: "#434b56", border: "0px solid #434b56", borderRadius: "3px", color: "#dee2e6" }}
                                type="text"
                                className="form-control"
                                id="taiKhoan"
                                placeholder="Nhp ti khon"
                                onChange={formik.handleChange} />
                            {/* {formik.errors.taiKhoan && formik.touched.taiKhoan ? <p className="text-danger">{formik.errors.taiKhoan}</p> : null} */}
                            <p className="text-danger">{formik.errors.taiKhoan}</p>
                            {/* {(formik.touched.taiKhoan && formik.errors.taiKhoan) ? <p className="text-danger">{formik.errors.taiKhoan}</p> : ""} */}
                        </div>
                        <div className="col-md-6 mb-3">
                            <label htmlFor="matKhau">Mt khu</label>
                            <input style={{ height: "35px", backgroundColor: "#434b56", border: "0px solid #434b56", borderRadius: "3px", color: "#dee2e6" }} type="text" className="form-control" id="matKhau" placeholder="Nhp mt khu" onChange={formik.handleChange} />
                            <p className="text-danger">{formik.errors.matKhau}</p>
                            {/* {(formik.touched.matKhau && formik.errors.matKhau) ? <p className="text-danger">{formik.errors.matKhau}</p> : ""} */}
                        </div>
                        <div className="col-md-6 mb-3">
                            <label htmlFor="hoTen">H Tn</label>
                            <input style={{ height: "35px", backgroundColor: "#434b56", border: "0px solid #434b56", borderRadius: "3px", color: "#dee2e6" }} type="text" className="form-control" id="hoTen" placeholder="Nhp h v tn" onChange={formik.handleChange} />
                            <p className="text-danger">{formik.errors.hoTen}</p>
                            {/* {formik.touched.hoTen && formik.errors.hoTen  ? <p className="text-danger">{formik.errors.hoTen}</p> : ""} */}
                        </div>
                        <div className="col-md-6 mb-3">
                            <label htmlFor="sdt">S in thoi</label>
                            <input style={{ height: "35px", backgroundColor: "#434b56", border: "0px solid #434b56", borderRadius: "3px", color: "#dee2e6" }} type="text" className="form-control" id="soDt" placeholder="Nhp s in thoi" onChange={formik.handleChange} />
                            <p className="text-danger">{formik.errors.soDt}</p>
                            {/* {formik.touched.soDt && formik.errors.soDt ? <p className="text-danger">{formik.errors.soDt}</p> : ""} */}
                        </div>
                        <div className="col-md-6 mb-3">
                            <label htmlFor="maLoaiNguoiDung">M loi ngi dng </label>
                            <select
                                style={{ height: "35px", backgroundColor: "#434b56", border: "0px solid #434b56", borderRadius: "3px", color: "#dee2e6" }}
                                name="maLoaiNguoiDung"
                                className="custom-select"
                                onChange={formik.handleChange}
                                onBlur={formik.handleBlur}>
                                <option value="" hidden>Chn m</option>
                                <option value="KhachHang">KhachHang</option>
                                <option value="QuanTri">QuanTri</option>
                            </select>
                            <p className="text-danger">{formik.errors.maLoaiNguoiDung}</p>
                            {/* {formik.touched.maLoaiNguoiDung && formik.errors.maLoaiNguoiDung ? <p className="text-danger">{formik.errors.maLoaiNguoiDung}</p> : ""} */}
                        </div>
                        <div className="col-md-6 mb-3">
                            <label htmlFor="maNhom">M nhm</label>
                            <input style={{ height: "35px", backgroundColor: "#282c31", border: "0px solid #434b56", borderRadius: "3px", color: "#dee2e6" }} type="text" className="form-control" id="maNhom" placeholder="GP09" disabled />
                            {/* <p className="text-danger">{formik.errors.maNhom}</p> */}
                        </div>
                        <div className="col-md-6 mb-3">
                            <label htmlFor="email">Email</label>
                            <input style={{ height: "35px", backgroundColor: "#434b56", border: "0px solid #434b56", borderRadius: "3px", color: "#dee2e6" }} type="email" className="form-control" id="email" placeholder="Nhp email" onChange={formik.handleChange} />
                            <p className="text-danger">{formik.errors.email}</p>
                            {/* {formik.touched.email && formik.errors.email  ? <p className="text-danger">{formik.errors.email}</p> : ""} */}
                        </div>
                    </div>
                </form>
           
           </div>
            <div className="modal-footer">
                <button type="button" id="btnThemThanhVien" className="btn btn__add_calendar" onClick={handleSubmit}>Thm thnh vin</button>
            </div>
        </div>
    )
}

