import axios from 'axios'
import { ACCESS_TOKEN, TICKET_INFORMATION, XOA_GHE_DANG_CHON } from '../../util/Setting'
import {showMessageAlert,showMessageAlertEvent} from '../../Template/SweetAlert'

export const ticketInformationAction = (maLichChieu) => { 
    return async (dispatch) => {
        try {
            const result = await axios({
                url: `https://movie0706.cybersoft.edu.vn/api/QuanLyDatVe/LayDanhSachPhongVe?MaLichChieu=${maLichChieu}`,
                method: 'GET',
            })
            dispatch({
                type: TICKET_INFORMATION,
                ticketInformation: result.data
            })
        } catch (errors) {
            console.log('errors', errors.response?.data)
        }
    }
}

export const orderTicket = (ticket) => { 
    return async (dispatch) => {
        try {
            const result = await axios({
                url: `https://movie0706.cybersoft.edu.vn/api/QuanLyDatVe/DatVe`,
                method: 'POST',
                data: ticket,
                headers: { 'Authorization': 'Bearer ' + localStorage.getItem(ACCESS_TOKEN) }
            })
            dispatch(ticketInformationAction(ticket.maLichChieu))
            dispatch({
                type: XOA_GHE_DANG_CHON
            })
            showMessageAlertEvent("t v thnh cng", "Hy kim tra email. Chc bn xem phim vui v!!","success")
        } catch (errors) {
            showMessageAlert("t v tht bi", `${errors.response?.data}`,"error")
        }
    }
}