import React, { useEffect, useState } from 'react'
import { NavLink } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
import { deleteMovieAction, getAllMovieAction, getAllMoviePage } from '../../../redux/Action/MovieAction'
import Pagination from "react-js-pagination";
import AddMovie from './AddMovie';
import UpdateMovie from './UpdateMovie';
import moment from 'moment';
export default function MovieManagement() {
    // searchMovie:
    const { allMovie, allMoviePage } = useSelector(state => state.MovieReducer)
    // console.log(allMovie)
    const dispatch = useDispatch();
    // search value for movie 
    const [searchValue, setSearchValue] = useState('')
    //Pagination:
    const [state, setState] = useState({
        activePage: 1
    })
    // Update Movie
    const [updateMovie, setUpdateMovie] = useState(true)
    // Choose IDs for update
    const [movieID, setMovieID] = useState("");

    // searchValue
    const handleValue = (e) => {
        setSearchValue(e.target.value)
    }
    // pagination
    const handlePageChange = (pageNumber) => {
        console.log(`active page is ${pageNumber}`);
        const newState = { activePage: pageNumber }
        setState(newState);
        dispatch(getAllMoviePage(pageNumber))
    }
    // console.log(allMoviePage)
    let countAllMovie = 0;
    for (let i = 0; i < allMovie.length; i++) {
        countAllMovie++
    }
    // console.log(countAllMovie)
    const renderMovieSearch = () => {
        let arrayItem = allMovie.filter(id => id.tenPhim?.toLowerCase().includes(searchValue.toLowerCase()))
        return arrayItem.map((movie, index) => {
            return <tr key={index}>
                <td>{index += 1}</td>
                <td>{movie?.maPhim}</td>
                <td>{movie?.tenPhim}</td>
                {/* <td>{movie?.biDanh}</td> */}
                <td>{movie?.trailer}</td>
                <td>{moment(movie?.ngayKhoiChieu).format("DD/MM/YYYY")}</td>
                <td>{movie.danhGia}</td>
                {/* <td>{movie?.maNhom}</td> */}
                <td><img style={{ width: 50 }} src={movie?.hinhAnh} alt={movie?.tenPhim}></img></td>
                {/* <td>{movie?.moTa}</td> */}
                <td><button className="btn__edit"
                    data-toggle="modal" data-target="#exampleModal"
                    onClick={() => {
                        setMovieID(movie)
                        setUpdateMovie(false)
                    }}
                ><i class='bx bxs-edit'></i></button></td>
                <td><button className="btn__del" onClick={() => {
                    dispatch(deleteMovieAction(movie?.maPhim))
                }}><i class='bx bx-trash'></i></button></td>
            </tr>
        })
    }

    const renderAllMoviePage = () => {
        return allMoviePage.items?.map((movie, index) => {
            return <tr key={index}>
                <td>{index += 1}</td>
                <td>{movie?.maPhim}</td>
                <td>{movie?.tenPhim}</td>
                {/* <td>{movie?.biDanh}</td> */}
                <td>{movie?.trailer}</td>
                <td>{moment(movie?.ngayKhoiChieu).format("DD/MM/YYYY")}</td>
                <td>{movie.danhGia}</td>
                {/* <td>{movie?.maNhom}</td> */}
                <td><img style={{ width: 50 }} src={movie?.hinhAnh} alt={movie?.tenPhim}></img></td>
                {/* <td>{movie?.moTa}</td> */}
                <td><button className="btn__edit" data-toggle="modal" data-target="#exampleModal"
                    onClick={() => {
                        setMovieID(movie)
                        setUpdateMovie(false)
                    }}><i class='bx bxs-edit'></i></button></td>
                <td><button className="btn__del" onClick={() => {
                    dispatch(deleteMovieAction(movie?.maPhim))
                }}><i class='bx bx-trash'></i></button></td>
            </tr>
        })
    }

    useEffect(() => {
        dispatch(getAllMoviePage(1))
        dispatch(getAllMovieAction())
    }, [dispatch])
    return (
        <div className="movie__manager"> 
            <div>
                
                <div className="container-fluid mt-5">
                    <div className="row">
                    <nav className="col-md-2 col-lg-2 d-md-block bg-dark sidebar__menu sidebar ">
                            <div className="sidebar-sticky pt-3">
                                <ul className="nav flex-column mt-5">
                                    <li className="nav-item">
                                        
                                        <NavLink className="nav-link " to='/admin/UsersManagement' >
                                        <i class='bx bxs-user-account'></i> Qun l ngi dng <span className="sr-only">(current)</span>
                                        </NavLink>
                                    </li>
                                    <li className="nav-item">
                                        <NavLink className="nav-link nav__active" to="/admin/MovieManagement">
                                        <i class='bx bx-film' ></i> Qun l phim
                                        </NavLink>
                                    </li>
                                    <li className="nav-item">
                                        <NavLink className="nav-link" to="/admin/CalendarMovieManagement">
                                        <i class='bx bx-calendar-star' ></i> Qun l lch chiu
                                        </NavLink>
                                    </li>

                                </ul>
                            </div>
                        </nav>
                        <main role="main" className="col-md-9 ml-sm-auto col-lg-10 mt-5">
                            {/* <nav className="my-3" aria-label="breadcrumb">
                                <ol className="breadcrumb">
                                    <li className="breadcrumb-item"><a href="/admin">Admin</a></li>
                                    <li className="breadcrumb-item active" aria-current="page">Movie Management</li>
                                </ol>
                            </nav> */}
                            <h4 className="text-center title__user__manager">Qun l phim</h4>

                            <div>
                                <div className="add">
                                    {/* Button trigger modal */}
                                    <button className="btn add_film" data-toggle="modal" data-target="#exampleModal"
                                        onClick={() => {
                                            setUpdateMovie(true)
                                        }} >
                                        Thm phim
                                    </button>

                                </div>
                                <div className="row my-3">
                                    <div className="col-3 ">
                                        <input
                                            className=" search__user form-control form-control-dark w-100"
                                            type="text"
                                            placeholder="Tm kim"
                                            aria-label="Search"
                                            onChange={handleValue}
                                        />
                                    </div>

                                   
                                </div>
                                {/* <div className="add">
                                    <button data-toggle="modal" data-target="#exampleModal" className="btn add__user"
                                     onClick={() => {
                                            setUpdateMovie(true)
                                        }} >
                                        Thm phim</button>

                                </div> */}
                                <div className="col-md-12">
                                   
                                    <div className="table user__manager__table">
                                        <table>
                                            <thead>
                                                <tr>
                                                    <th>STT</th>
                                                    <th>M phim</th>
                                                    <th>Tn phim</th>
                                                    {/* <th>B danh</th> */}
                                                    <th>Link trailer</th>
                                                    <th>Ngy khi chiu</th>
                                                    {/* <th>M nhm</th> */}
                                                    <th>nh gi</th>
                                                    <th>Hnh nh</th>
                                                    {/* <th>M t</th> */}
                                                    <th></th>
                                                    <th></th>
                                                </tr>

                                            </thead>
                                            <tbody>
                                                {searchValue === '' ? renderAllMoviePage() : renderMovieSearch()}
                                            </tbody>
                                        </table>

                                    </div>

                                </div>

                            </div>
                            <div className="col-4 user__pagination">
                                        <Pagination
                                            itemClass="page-item"
                                            linkClass="page-link"
                                            activePage={state.activePage}
                                            itemsCountPerPage={1}
                                            totalItemsCount={countAllMovie / 10}
                                            pageRangeDisplayed={10}
                                            onChange={handlePageChange.bind()}
                                        />
                                    </div>
                        </main>
                    </div>
                </div>
                <div className="modal fade" id="exampleModal" tabIndex={-1} role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                    <div className="modal-dialog" role="document">
                        {updateMovie ? <AddMovie /> : <UpdateMovie movie={movieID} />}
                    </div>
                </div>
            </div>
        </div>
    )
}

