
import React from 'react'
import { useSelector } from 'react-redux'
import { NavLink } from "react-bootstrap";
import DropDownAdmin from '../../Pages/Login/DropDownAdmin'
import LogInLogOutButton from '../../Pages/Login/LogInLogOutButton'


export default function Header() {
    const { taiKhoan } = useSelector(state => state.UsersReducer)
    return (
        <div className="wrapper" >
            <div className="container">
                <div className="navbar navbar-expand-lg nav" >
                    <a href="/" className="logo">
                        <i className="bx bx-film bx-tada main-color" />T
                        <span className="main-color">i</span>x
                    </a>
                    <button className="navbar-toggler navbar-toggler-right collapsed" type="button" data-toggle="collapse" data-target="#collapsingNavbar" aria-label="Toggle navigation">
                    <i style={{color:"#fff"}} className='bx bx-menu'></i>
                    </button>
                    <div className="collapse navbar-collapse menu__right" id="collapsingNavbar">
                        <ul className="navbar-nav mt-2 mt-lg-0 nav__menu ">
                            <li className="nav-item">
                                <a className="nav-link" href="/#movie__schedule" >Lch chiu</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" href="/#cinema" >Cm rp</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" href="/#news" >Tin tc</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" href="/#application" >ng dng</a>
                            </li>
                            <li className="">
                                {taiKhoan.trim() !== '' ? <DropDownAdmin /> : <LogInLogOutButton />}
                            </li>
                        </ul>
                    </div>
                   
                </div>
            </div>

        </div>

    )
}
