import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import { useSpring, animated } from 'react-spring/web.cjs'; // web.cjs is required for IE 11 support
import { NavLink } from 'react-router-dom';

const useStyles = makeStyles((theme) => ({
    modal: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
    },
    paper: {
        // backgroundColor: theme.palette.background.paper,
        // border: '2px solid #000',
        // boxShadow: theme.shadows[5],
        // padding: theme.spacing(2, 4, 3),
    },
}));

const Fade = React.forwardRef(function Fade(props, ref) {
    const { in: open, children, onEnter, onExited, ...other } = props;
    const style = useSpring({
        from: { opacity: 0 },
        to: { opacity: open ? 1 : 0 },
        onStart: () => {
            if (open && onEnter) {
                onEnter();
            }
        },
        onRest: () => {
            if (!open && onExited) {
                onExited();
            }
        },
    });

    return (
        <animated.div ref={ref} style={style} {...other}>
            {children}
        </animated.div>
    );
});

Fade.propTypes = {
    children: PropTypes.element,
    in: PropTypes.bool.isRequired,
    onEnter: PropTypes.func,
    onExited: PropTypes.func,
};

export default function PlayVideoMovieShowTime(props) {
    const classes = useStyles();
    const [open, setOpen] = React.useState(false);

    const handleOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };
    // const phim = props.movie;
    // console.log("phim", phim);
    return (
        <div style={{ height: 400, width: 250 }} id="showing__time">
            <div className="overlay">
                <NavLink to={`/detail/${props.movie?.maPhim}`} > 
                <img className="img-fluid" style={{ height:330, width: 250 }} src={props.movie?.hinhAnh} alt={props.movie.tenPhim} />
                </NavLink>
                <button className="btn btn__play__video" type="button" onClick={handleOpen}>
                    <i class="far fa-play-circle"></i>
                </button>
                <div className="title__movie">
                    <div className="movie__detail">
                        <p>{props.movie?.tenPhim}</p>
                        <span>{props.movie.maPhim % 2 == 0 ? "120 pht - 3.9 IMDb" : " 110 pht - 6.2 IMDb"}</span>
                    </div>
                    <NavLink className="btn  btn__buy__ticket" to={`/detail/${props.movie?.maPhim}`}  >t v</NavLink>
                </div>
            </div>


            <Modal
                aria-labelledby="spring-modal-title"
                aria-describedby="spring-modal-description"
                className={classes.modal}
                open={open}
                onClose={handleClose}
                closeAfterTransition
                BackdropComponent={Backdrop}
                BackdropProps={{
                    timeout: 500,
                }}
            >
                <Fade in={open}>
                    <div className={classes.paper}>
                        <iframe
                            width="700"
                            height="400"
                            src={props.movie?.trailer} title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
                    </div>
                </Fade>
            </Modal>
        </div>
    );
}