import Swal from 'sweetalert2'

export const showMessageAlert = (title, message, type) => {
    Swal.fire({
        type: type,
        title: title,
        text: message,
        icon: type
    })
}

export const showMessageAlertEvent = (title, message, type) => {
    Swal.fire({
        type: type,
        title: title,
        text: message,
        icon: type,
        preConfirm: () => {
            window.location.reload();
        }
    }).then((result) => {
        if (result.value) {
        }
    })
}

export const showMessageDelete = (title, message, type) => {
    Swal.fire({
        title: title,
        text: message,
        icon: type,
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Chc chn!',
        cancelButtonText: 'Quay li!', 
      }).then((result) => {
        if (result.isConfirmed) {
          Swal.fire(
            'Xa thnh cng!',
            "",
            'success'
          )
        }
      })
}

