import React from 'react'
import { NavLink } from 'react-router-dom'
import * as Yup from 'yup'
import { useFormik } from 'formik'
import { useDispatch } from 'react-redux'
import { registerAction } from '../../redux/Action/UsersAction'


export default function Register() {
    const dispatch = useDispatch()
    const formik = useFormik({
        initialValues: {
            taiKhoan: '',
            matKhau: '',
            email: '',
            soDt: '',
            maNhom: 'GP09',
            maLoaiNguoidung: 'KhachHang',
            hoTen: '',
            matKhauConfirmation: ''
        },
        validationSchema: Yup.object().shape({
            taiKhoan: Yup.string().required('Ti khon khng c b trng'),
            matKhau: Yup.string().required('Mt khu khng c b trng').min(2, "Mt khu ti thiu 2 k t").max(32, "Mt khu ti a 32 k t"),
            matKhauConfirmation: Yup.string()
                .oneOf([Yup.ref('matKhau'), null], 'Mt khu khng trng khp'),
            hoTen: Yup.string().required('H tn khng c b trng'),
            soDt: Yup.string().required('S in thoi khng c b trng'),
            email: Yup.string().required('Email khng c b trng'),
        }),

    })
    const handleSubmit = () => {
        console.log(formik.isValid)
        // console.log(formik.isValid.values)
        console.log(formik.values)
        if (formik.isValid) {
            console.log(formik.values)
            dispatch(registerAction(formik.values))
        }
        else {
            
            // alert("Thng tin ng k khng hp l !");
        }
    }
    console.log(formik.values)
    return (
      
            <div className="card" id="register">
                <div className="register__wrappe">
                    <article className="card-body" >
                        <img className=" text-center" src={require("../../assets/img/logo/loginLogo.png").default} alt="logo" />
                        
                        <form className="">
                            <div className="form-group">
                                <div className="input-group-prepend">
                                    <span className="input-group-text mr-1"><i className='bx bx-user'></i></span>
                                    <input className="form-control" placeholder="Ti khon" id="taiKhoan" type="text" onBlur={formik.handleBlur} onChange={formik.handleChange} />
                                </div>
                                {formik.errors.taiKhoan && formik.touched.taiKhoan ? <p className="text-danger">{formik.errors.taiKhoan}</p> : null}
                            </div>
                            <div className="form-group">
                                <div className="input-group-prepend">
                                    <span className="input-group-text mr-1"> <i class='bx bxs-lock-alt'></i> </span>
                                    <input className="form-control" placeholder="Mt khu" id="matKhau" type="password" onBlur={formik.handleBlur} onChange={formik.handleChange} />
                                </div>
                                {formik.errors.taiKhoan && formik.touched.matKhau ? <p className="text-danger">{formik.errors.matKhau}</p> : null}
                            </div>
                            <div className="form-group">
                                <div className="input-group-prepend">
                                    <span className="input-group-text mr-1"> <i class='bx bxs-lock-alt'></i></span>
                                    <input className="form-control" placeholder="Nhp li mt khu" id="matKhauConfirmation" type="password" onBlur={formik.handleBlur} onChange={formik.handleChange} />
                                </div>
                                <p className="text-danger">{formik.errors.matKhauConfirmation}</p>
                            </div>
                            <div className="form-group">
                                <div className="input-group-prepend">
                                    <span className="input-group-text mr-1"> <i className='bx bx-user'></i></span>
                                    <input className="form-control" placeholder="H v tn" id="hoTen" type="text" onBlur={formik.handleBlur} onChange={formik.handleChange} />
                                </div>
                                {formik.errors.taiKhoan && formik.touched.hoTen ? <p className="text-danger">{formik.errors.hoTen}</p> : null}
                            </div>
                            <div className="form-group">
                                <div className="input-group-prepend">
                                    <span className="input-group-text mr-1"> <i class='bx bxs-message-detail'></i></span>
                                    <input className="form-control" placeholder="Email" type="email" id="email" onBlur={formik.handleBlur} onChange={formik.handleChange} />
                                </div>
                                {formik.errors.taiKhoan && formik.touched.email ? <p className="text-danger">{formik.errors.email}</p> : null}
                            </div>
                            <div className="form-group">
                                <div className="input-group-prepend">
                                    <span className="input-group-text mr-1"> <i class='bx bxs-phone'></i> </span>
                                    <input className="form-control" placeholder="S in thoi" type="text" id="soDt" onBlur={formik.handleBlur} onChange={formik.handleChange} />
                                </div>
                                {formik.errors.taiKhoan && formik.touched.soDt ? <p className="text-danger">{formik.errors.soDt}</p> : null}
                            </div>


                            <div className="form-group">
                                <button type="button" className="btn btn-register" onClick={handleSubmit}>ng k</button>
                            </div>
                            <p className="text-center">Nu bn cha c ti khon,<NavLink className="text-danger" to="/login"> ng nhp ti y</NavLink> </p>
                        </form>
                    </article>
                </div>
            </div>


       
    )
}
