import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { NavLink } from 'react-router-dom'
import { history } from '../../App'
import { LOGOUT_SUCCESS } from '../../util/Setting'
import {showMessageAlert} from '../../Template/SweetAlert'
export default function DropDownAdmin(props) {
    const { user } = useSelector(state => state.UsersReducer)
    const dispatch = useDispatch()

    const handleLogOut = () => {
        dispatch({
            type: LOGOUT_SUCCESS
        })
        localStorage.removeItem('USER_LOGIN')
        localStorage.removeItem('ACCESS_TOKEN')
        showMessageAlert("ng xut thnh cng","", "success");
        history.push('/');
    }
    return (
        <div  >
            <div className="dropdown btn-group">
                <button className="btn dropdown-toggle" style={{ backgroundColor :"transparent", color:"#fff" }} type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
               
                {/* <img src="https://i.pravatar.cc/30" alt=""></img> */}
                <i className='bx bx-user' ></i>
                {user.hoTen}
                </button>
                <div className="dropdown-menu " aria-labelledby="dropdownMenuButton">
                    {user.maLoaiNguoiDung === "QuanTri" ? <NavLink to="/admin/UsersManagement"><button className="dropdown-item" data-toggle="collapse" data-target="#collapsingNavbar" aria-expanded="false">Admin</button></NavLink> : ""}
                    <NavLink to="/UserProfile"><button className="dropdown-item" data-toggle="collapse" data-target="#collapsingNavbar" aria-expanded="false">Thng tin c nhn </button></NavLink>
                    <button onClick={handleLogOut} className="dropdown-item " data-toggle="collapse" data-target="#collapsingNavbar" aria-expanded="false">ng xut</button>
                </div>
            </div>
        </div>

    )
}
