import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Link } from 'react-router-dom';
import { getDetailMovieAction } from '../../redux/Action/MovieAction'
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import ListSubheader from '@material-ui/core/ListSubheader';
import Box from '@material-ui/core/Box';
import moment from 'moment';
import {showMessageAlert} from '../../Template/SweetAlert'
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';











function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`simple-tabpanel-${index}`}
            aria-labelledby={`simple-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box p={3}>
                    <Typography>{children}</Typography>
                </Box>
            )}
        </div>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired,
};

function a11yProps(index) {
    return {
        id: `simple-tab-${index}`,
        'aria-controls': `simple-tabpanel-${index}`,
    };
}

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        backgroundColor: "transparent",
        height: 400,
    },
    formControl: {
        // margin: theme.spacing(1),
        marginRight:"50px",
        marginBottom:"50px",
        minWidth: "40%",
        backgroundColor: "transparent",
        // display: "block",
    },
    icon: {
        color:"#fff"
    }
    // selectMenu: {
    //     color:"red",
    // },

}));

export default function Detail(props) {
    const classes = useStyles();
    const [value, setValue] = React.useState(0);

    const [age, setAge] = React.useState('');
    const [open, setOpen] = React.useState(false);

    const handleChangeSelect = (event) => {
        setAge(event.target.value);
    };

    const handleClose = () => {
        setOpen(false);
    };

    const handleOpen = () => {
        setOpen(true);
    };



    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    // console.log(props)
    const [chosenCinemaBrand, setchosenCinemaBrand] = useState("");
    const [chosenCinema, setchosenCinema] = useState("");
    const [chosenDate, setchosenDate] = useState("");
    const [chosenTime, setchosenTime] = useState("");
    const [chosenmaLichChieu, setchosenmaLichChieu] = useState("");
    const { detailMovie } = useSelector(state => state.MovieReducer)
    console.log("detailMovie", detailMovie);
    const dispatch = useDispatch()
    const id = props.match.params.id
    const [buttonForSellTikcet, setbuttonForSellTikcet] = useState(true)


    useEffect(() => {
        dispatch(getDetailMovieAction(id))
    }, [])
    useEffect(() => {
        // console.log(`${chosenDate}T${chosenTime}`)
        setchosenmaLichChieu(chosenCinema?.lichChieuPhim?.find(item => item.ngayChieuGioChieu === `${chosenDate}T${chosenTime}`).maLichChieu)
    }, [chosenTime])

    function removeDuplicates(array) {
        let a = [];
        array?.map((x, index) => {
            if (!a.includes(x)) {
                a.push(x);
            }
        });
        return a;
    }
    const renderChooseCinemaBrand = () => {
        return detailMovie.heThongRapChieu?.map((cinemaBrand, index) => {
            return (
                <option  value={cinemaBrand.maHeThongRap} key={index}>
                    {cinemaBrand.tenHeThongRap}
                </option >
            );
        });
    };
    const renderChooseCinema = () => {
        return chosenCinemaBrand.cumRapChieu?.map((cinema, index) => {
            return (
                <option value={cinema.maCumRap} key={index}>
                    {cinema.tenCumRap}
                </option>
            );
        });
    };
    const renderChooseDate = () => {
        let DateTimeArr = chosenCinema.lichChieuPhim?.map((item, index) => {
            return item.ngayChieuGioChieu.split("T")[0];
        });
        let DateArr = removeDuplicates(DateTimeArr);
        return DateArr.map((item, index) => {
            return (
                <option value={item} key={index}>
                    {item}
                </option>
            );
        });
    };
    const renderChooseTime = () => {
        let DateTimeArr = chosenCinema.lichChieuPhim?.map((item, index) => {
            return item.ngayChieuGioChieu.split("T");
        });
        return DateTimeArr?.filter((item) => item[0] === chosenDate).map(
            (item, index) => {
                return (
                    <option value={item[1]} key={index}>
                        {item[1]}
                    </option>
                );
            }
        );
    };
    const resetCinemaOption = (id) => {
        document.getElementById(id).value = ""
    }


    return (
        <div className="detail py-5" style={{ backgroundImage: `url(${detailMovie.hinhAnh})` }}>
            <div className="container" id="responsiveDetail">
                <div className="row mt-5" >
                    <div className="col-3 container-fluid" >
                        <img style={{ width: '100%', height: "400px" }} src={detailMovie.hinhAnh} alt="hinhAnh" />
                    </div>

                    <div className="col-9  detail__content">
                        <div className={classes.root}>
                            <AppBar position="static" color="transparent">
                                <Tabs TabIndicatorProps={{ style: { background: "#fb4226" } }} value={value} onChange={handleChange} aria-label="simple tabs example">
                                    <Tab label="Thng tin" {...a11yProps(0)} />
                                    <Tab label="Lch chiu" {...a11yProps(1)} />

                                </Tabs>
                            </AppBar>
                            <TabPanel value={value} index={0}>
                                <div className="row">
                                    <div className="col-3"><h5 className="content__title ">Tn phim  </h5></div>
                                    <div className="col-9"><span className="content__detail">{detailMovie.tenPhim}</span></div>
                                </div>
                                <div className="row">
                                    <div className="col-3"><h5 className="content__title ">o din  </h5></div>
                                    <div className="col-9"><span className="content__detail">{detailMovie.maPhim % 2 == 0 ? "Joe Russo" : "Anthony Russo"}</span></div>
                                </div>
                                <div className="row">
                                    <div className="col-3"><h5 className="content__title ">Din Vin  </h5></div>
                                    <div className="col-9"><span className="content__detail">{detailMovie.maPhim % 3 == 0 ? "Robert Downey, Chris Evans, Chris Hemsworth" : "Tom Hiddleston, Anthony Mackie"}</span></div>
                                </div>
                                <div className="row">
                                    <div className="col-3"><h5 className="content__title ">nh gi  </h5></div>
                                    <div className="col-9"><span className="content__detail">{detailMovie.danhGia}</span></div>
                                </div>
                                <div className="row">
                                    <div className="col-3"><h5 className="content__title ">Ngy khi chiu  </h5></div>
                                    <div className="col-9"><span className="content__detail">{moment(detailMovie.ngayKhoiChieu).format("DD.MM.YYYY")}</span></div>
                                </div>
                                <div className="row">
                                    <div className="col-3"><h5 className="content__title ">Ni dung  </h5></div>
                                    <div className="col-9"><span className="content__detail">{detailMovie.moTa}</span></div>
                                </div>

                            </TabPanel>
                            <TabPanel value={value} index={1}>
                                <FormControl className={classes.formControl}>
                                    <InputLabel className={classes.formControl} style={{ color: "#e64212", position:"none" }} htmlFor="grouped-select0">Chn rp</InputLabel>
                                    <Select  
                                   
                                    style={{ color: "#dee2e6" }} 
                                     defaultValue="" 
                                    id="grouped-select0"
                                    onChange={(e) => {
                                       
                                        setchosenCinemaBrand(
                                            detailMovie?.heThongRapChieu.find(item => item.maHeThongRap === e.target.value)
                                        )
                                        setbuttonForSellTikcet(true)
                                        resetCinemaOption("grouped-select")
                                        resetCinemaOption("grouped-select2")
                                        resetCinemaOption("grouped-select3")

                                    }}
                                    >  
                                       {renderChooseCinemaBrand()}
                                    </Select>
                                </FormControl>
                                <FormControl className={classes.formControl}>
                                    <InputLabel className={classes.formControl} style={{ color: "#e64212" }} htmlFor="grouped-select">Chn a ch rp</InputLabel>
                                    <Select 
                                    
                                    style={{ color: "#dee2e6" }}  
                                    defaultValue="" 
                                    id="grouped-select"
                                    onChange={(e) => {
                                       
                                        setchosenCinema(
                                            chosenCinemaBrand?.cumRapChieu.find(
                                                (item) => item.maCumRap === e.target.value
                                            )
                                        );
                                        setbuttonForSellTikcet(true)
                                        resetCinemaOption("grouped-select2")
                                        resetCinemaOption("grouped-select3")
                                    }}
                                    >
                                       {renderChooseCinema()}
                                    </Select>
                                </FormControl>
                                <FormControl className={classes.formControl}>
                                    <InputLabel className={classes.formControl} style={{ color: "#e64212" }} htmlFor="grouped-select2">Chn ngy chiu phim</InputLabel>
                                    <Select 
                                 
                                    style={{ color: "#dee2e6" }}  
                                    defaultValue="" 
                                    id="grouped-select2"
                                    onChange={(e) => {
                                        
                                        setchosenDate(
                                            e.target.value
                                        )
                                        setbuttonForSellTikcet(true)
                                        resetCinemaOption("grouped-select3")
                                    }}
                                    >
                                       {renderChooseDate()}
                                    </Select>
                                </FormControl>
                                <FormControl  className={classes.formControl}>
                                    <InputLabel className={classes.formControl} style={{ color: "#e64212" }} htmlFor="grouped-select3">Chn gi chiu phim</InputLabel>
                                    <Select 
                                    
                                    style={{ color: "#dee2e6" }}  
                                    defaultValue="" 
                                    id="grouped-select3"
                                    onChange={(e) => {
                                       
                                        setchosenTime(
                                            e.target.value
                                        )
                                        setbuttonForSellTikcet(false)
                                    }}
                                    >
                                       {renderChooseTime()}
                                    </Select>
                                </FormControl>
                            


                                {buttonForSellTikcet ? <button className="btn buy__ticket" 
                                    onClick={() => { showMessageAlert("t v tht bi","Vui lng chn y  thng tin!","error") }}>
                                <i className="bx bxs-hand-right mr-3" />
                                <span>t v</span>
                                    </button> : 
                                <Link className="btn buy__ticket" to={`/sellticket/${chosenmaLichChieu}`} type="button" ><i className="bx bxs-hand-right mr-3" />
                                <span>t v</span></Link>}

                            </TabPanel>

                        </div>
                    </div>
                </div>

            </div>

        </div>
    )
}
