import React, { useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import Slider from "react-slick";
import { getAllMovieAction } from '../../redux/Action/MovieAction.js';
import PlayVideo from './PlayVideo'
import { NavLink } from 'react-router-dom'

export default function Carousel() {

  const { movieCarousel } = useSelector(state => state.MovieReducer)
  console.log("movieCarousel", movieCarousel);
  const dispatch = useDispatch()

  useEffect(() => {
    dispatch(getAllMovieAction())
  }, [dispatch])


  function SampleNextArrow(props) {
    const { onClick } = props;
    return (
      <span className="arrow-right-slick" onClick={onClick}>
        <i class="bx bxs-chevrons-right"></i>
      </span>
    );
  }
  function SamplePrevArrow(props) {
    const { className, style, onClick } = props;
    return (
      <span className="arrow-left-slick" onClick={onClick}>
        <i class="bx bxs-chevrons-left"></i>
      </span>
    );
  }
  const settings = {
    dots: false,
    infinite: true,
    slidesToShow: 1,
    slidesToScroll: 1,

    nextArrow: <SampleNextArrow />,
    prevArrow: <SamplePrevArrow />,
    autoplay: true,
    speed: 1500,
    autoplaySpeed: 3000,
    // cssEase: "linear",
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          infinite: true,
          dots: false,
          speed: 1000,
        }
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          initialSlide: 2,
          speed: 1000,
          dots: false,
        }
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          speed: 500,
          dots: false,
        }
      }
    ]
  };

  const renderCarousel = () => {
    return movieCarousel.map((movie, index) => {
      return <div className="hero-slide-item img__carousel" key={index}>
        <img src={movie.hinhAnh} alt=""/>
        <div className="overlay" />
        <div className="hero-slide-item-content">
          <div className="item-content-wraper">
            <div className="item-content-title top-down mb-3">
              {movie.tenPhim}
            </div>
            <div className="movie-infos top-down delay-2 mb-3">
              <div className="movie-info">
                <i className="bx bxs-star" />
                <span>{movie.danhGia}</span>
              </div>
              <div className="movie-info">
                <i className="bx bxs-time" />
                <span>{movie.thoiLuong}</span>
              </div>
              <div className="movie-info">
              <i class='bx bx-fullscreen' ></i> 
                <span>{movie.doPhanGiai}</span>
              </div>
              <div className="movie-info">
                <i class='bx bx-user'></i>
                <span>{movie.doTuoiXem}</span>
              </div>
            </div>
            <div className="item-content-description top-down delay-4 mb-3">
            {movie.moTa}
            </div>
            <div className="item-action top-down delay-6">
              <NavLink to={`/detail/${movie.maPhim}`} className="btn btn-hover">
                <i className="bx bxs-hand-right" />
                <span>t v</span>
              </NavLink>
            </div>
          </div>
         
        </div>
        <div className="text-center">
                 <PlayVideo movie={movie} />
            </div>
      </div>
    })
   

  }

  return (
    <div className="carousel" >
      <Slider {...settings}>
        {renderCarousel()}
      </Slider>
    </div>


  )
}
