export const getAllMoviePage = (page) => {
  return async (dispatch) => {
    try {
      const result = await axios({
        url: `https://movie0706.cybersoft.edu.vn/api/QuanLyPhim/LayDanhSachPhimPhanTrang?maNhom=GP09&soTrang=${page}&soPhanTuTrenTrang=10`,
        method: "GET",
      });
      console.log("getAllMoviePage",result.data)
      dispatch({
        type: GET_ALL_MOVIE_PAGE,
        allMoviePage: result.data,
      });
    } catch (errors) {
      console.log("errors", errors.response?.data);
    }
  };
};
export const addMovieAction = (movie) => {
  return async (dispatch) => {
    try {
      const result = await axios({
        url: "https://movie0706.cybersoft.edu.vn/api/QuanLyPhim/ThemPhim",
        method: "POST",
        data: movie,
        headers: {
          Authorization: "Bearer " + localStorage.getItem(ACCESS_TOKEN),
        },
      });
      if (movie.picture) {
        let frm = new FormData();
        frm.append('file', movie.picture)
        frm.append('tenPhim', result.data.tenPhim)
        frm.append('maNhom', 'GP09')
        let rs = await axios({
          url: `https://movie0706.cybersoft.edu.vn/api/QuanLyPhim/UploadHinhAnhPhim`,
          method: "POST",
          data: frm,
          headers: {
            Authorization: `Bearer` + localStorage.getItem(ACCESS_TOKEN),
          },
        });
      }
      showMessageAlertEvent("Thm phim thnh cng", "","success")
    } catch (errors) {
      showMessageAlert("Thm phim tht bi", `${errors.response?.data}`,"error")
    }
  };
};

export const deleteMovieAction = (maPhim) => {
  return async (dispatch) => {
    try {
      const result = await axios({
        url: `https://movie0706.cybersoft.edu.vn/api/QuanLyPhim/XoaPhim?MaPhim=${maPhim}`,
        method: "DELETE",
        headers: {
          Authorization: "Bearer " + localStorage.getItem(ACCESS_TOKEN),
        },
      });
      dispatch(getAllMoviePage());
      dispatch(getAllMovieAction());
      showMessageAlertEvent("Xa phim thnh cng", "","success")
    } catch (errors) {
      showMessageAlert("Xa phim tht bi", `${errors.response?.data}`,"error")
    }
  };
};

export const updateMovieAction = (updateMovie) => {
  return async (dispatch) => {
    try {
      const result = await axios({
        url: `https://movie0706.cybersoft.edu.vn/api/QuanLyPhim/CapNhatPhim`,
        method: "POST",
        data: updateMovie,
        headers: {
          Authorization: "Bearer " + localStorage.getItem(ACCESS_TOKEN),
        },
      });
     
      if (updateMovie.picture) {
        let frm = new FormData();
        frm.append('File', updateMovie.picture)
        frm.append('tenPhim', result.data.tenPhim)
        frm.append('maNhom', 'GP09')
        let rs = await axios({
          url: `https://movie0706.cybersoft.edu.vn/api/QuanLyPhim/UploadHinhAnhPhim`,
          method: "POST",
          data: frm,
          headers: {
            Authorization: `Bearer` + localStorage.getItem(ACCESS_TOKEN),
          },
        });
      }
      dispatch(getAllMoviePage());
      dispatch(getAllMovieAction());
      showMessageAlertEvent("Cp nht phim thnh cng", "","success")
    } catch (errors) {
      showMessageAlert("Cp nht phim tht bi", `${errors.response?.data}`,"error")
    }
  };
};
