import axios from 'axios'
import { ACCESS_TOKEN, GET_ALL_USER, GET_ALL_USER_PAGE } from '../../util/Setting'
import {showMessageAlert, showMessageAlertEvent} from '../../Template/SweetAlert'


export const getAllUserPage = (page) => {
    return async (dispatch) => {
        try {
            const result = await axios({
                url: `https://movie0706.cybersoft.edu.vn/api/QuanLyNguoiDung/LayDanhSachNguoiDungPhanTrang?MaNhom=GP09&soTrang=${page}&soPhanTuTrenTrang=10`,
                method: 'GET'
            })
            // console.log('result', result.data);
            dispatch({
                type: GET_ALL_USER_PAGE,
                allUserPage: result.data
            })
        } catch (errors) {
            console.log('errors', errors.response?.data)
        }
    }
}

export const getAllUser = (maNhom = "GP09") => {
    return async (dispatch) => {
        try {
            const result = await axios({
                url: `https://movie0706.cybersoft.edu.vn/api/QuanLyNguoiDung/LayDanhSachNguoiDung?MaNhom=${maNhom}`,
                method: 'GET'
            })
            // console.log('result', result.data);
            dispatch({
                type: GET_ALL_USER,
                allUser: result.data
            })
        } catch (errors) {
            console.log('errors', errors.response?.data)
        }
    }
}

export const addUser = (addUser) => {
    return async (dispatch) => {
        try {
            const result = await axios({
                url: `https://movie0706.cybersoft.edu.vn/api/QuanLyNguoiDung/ThemNguoiDung`,
                method: 'POST',
                data: addUser,
                headers: { 'Authorization': 'Bearer ' + localStorage.getItem(ACCESS_TOKEN) }

            })
            showMessageAlertEvent("Thm ngi dng thnh cng","", "success");
            
        } catch (errors) {
            showMessageAlert("Thm ngi dng tht bi",`${errors.response?.data}`, "error");
        }
    }
}


export const deletedUser = (taiKhoan) => {
    return async (dispatch) => {
        try {
            const result = await axios({
                url: `https://movie0706.cybersoft.edu.vn/api/QuanLyNguoiDung/XoaNguoiDung?TaiKhoan=${taiKhoan}`,
                method: 'DELETE',
                headers: { 'Authorization': 'Bearer ' + localStorage.getItem(ACCESS_TOKEN) }

            })
            dispatch(getAllUserPage())
            dispatch(getAllUser())
            showMessageAlertEvent("Xa ngi dng thnh cng", "","success")

        } catch (errors) {
            showMessageAlertEvent("Xa ngi dng tht bi", `${errors.response.data}`,"error")
        }
    }
}


export const updateUserProfile = (updateUser) => {
    return async (dispatch) => {
        try {
            const result = await axios({
                url: `https://movie0706.cybersoft.edu.vn/api/QuanLyNguoiDung/CapNhatThongTinNguoiDung`,
                method: 'PUT',
                data: updateUser,
                headers: { 'Authorization': 'Bearer ' + localStorage.getItem(ACCESS_TOKEN) }
            })
            
            dispatch(getAllUserPage())
            dispatch(getAllUser())
            showMessageAlertEvent("Cp nht thnh cng", "","success")
            
        } catch (errors) {
            showMessageAlertEvent("Cp nht tht bi", `${errors.response.data}`,"error")
        }
    }
}

