import React, { useState } from 'react'
import { useFormik } from 'formik'
import * as Yup from 'yup'
import { useDispatch } from 'react-redux'
import { addMovieAction } from '../../../redux/Action/MovieAction'
import { formatDate } from '../../../util/GetDateFormatted'
import { removeAccents } from '../../../util/RemoveAccents'
import { DropzoneArea } from 'material-ui-dropzone'

export default function AddMovie() {
    const [picture, setPicture] = useState(null)
    const dispatch = useDispatch()
    const formik = useFormik({
        initialValues: {
            tenPhim: "",
            trailer: 1,
            danhGia: 0,
            hinhAnh: ".jpg",
            moTa: 3,
            maNhom: "GP09",
            ngayKhoiChieuValue: "",
            
        },
        validationSchema: Yup.object().shape({
            tenPhim: Yup.string().required('tn phim khng c b trng'),
            trailer: Yup.string().required('Trailer khng c b trng'),
            danhGia: Yup.string().required('nh gi khng c b trng'),
            moTa: Yup.string().required('M t khng c b trng'),
        }),
    })

    // console.log(formik.values)
    const handleSubmit = () => {
        const ngayKhoiChieu = formatDate(formik.values.ngayKhoiChieuValue)
        const biDanh = removeAccents(formik.values.tenPhim)
        const finalData = { ...formik.values, ngayKhoiChieu, biDanh, picture }
        // console.log(biDanh)
        if (formik.isValid) {
            dispatch(addMovieAction(finalData))
            // console.log(finalData)
        }
    }
  
    return (
        <div className="modal-content" style={{backgroundColor:"#323a46", color:"#dee2e6"}}>
            <div className="modal-header">
                <h5 className="modal-title" id="exampleModalLabel">Thm phim</h5>
                <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true"></span>
                </button>
            </div>
            <div className="card-body">
                <form onSubmit={formik.handleSubmit}>
                    <div className="row">
                        <div className="col-md-6 mb-3">
                            <label htmlFor="tenPhim">Tn phim</label>
                            <input 
                             style={{height:"35px", backgroundColor:"#434b56", border:"0px solid #434b56", borderRadius:"3px", color:"#dee2e6"}} 
                            type="text" className="form-control" id="tenPhim" placeholder="Nhp tn phim" onChange={formik.handleChange} />
                            <p className="text-danger">{formik.errors.tenPhim}</p>
                        </div>
                        <div className="col-md-6 mb-3">
                            <label htmlFor="trailer">Link trailer</label>
                            <input 
                             style={{height:"35px", backgroundColor:"#434b56", border:"0px solid #434b56", borderRadius:"3px", color:"#dee2e6"}} 
                            type="text" className="form-control" id="trailer" placeholder="Nhp link" onChange={formik.handleChange} />
                            <p className="text-danger">{formik.errors.trailer}</p>
                        </div>
                        <div className="col-md-6 mb-3">
                            <label htmlFor="ngayKhoichieu">Ngy khi chiu</label>
                            <input 
                             style={{height:"35px", backgroundColor:"#434b56", border:"0px solid #434b56", borderRadius:"3px", color:"#dee2e6"}} 
                            type="date" className="form-control" id="ngayKhoiChieuValue"
                                onChange={formik.handleChange} />
                            <p className="text-danger">{formik.errors.ngayKhoiChieu}</p>
                        </div>
                        <div className="col-md-6 mb-3">
                            <label htmlFor="danhGia">nh gi</label>
                            <input 
                             style={{height:"35px", backgroundColor:"#434b56", border:"0px solid #434b56", borderRadius:"3px", color:"#dee2e6"}} 
                            type="number" className="form-control" id="danhGia" placeholder="nh gi" onChange={formik.handleChange} />
                            <p className="text-danger">{formik.errors.danhGia}</p>
                        </div>
                    </div>
                    <div className="mb-3">
                        <div className="form-group">

                            <label htmlFor="hinhAnh">Hnh nh</label>
                            <div className="form-group" >
                                <DropzoneArea
                                    filesLimit={1}
                                    dropzoneClass="add_img"
                                    acceptedFiles={["image/*"]}      
                                    onChange={(image) => setPicture(image[0])}
                                    maxFileSize={50000000}
                                />
                            </div>
                            <p className="text-danger">{formik.errors.hinhAnh}</p>
                        </div>
                    </div>
                    <div className="mb-3">
                        <label htmlFor="moTa">M T</label>
                        <textarea 
                         style={{height:"100px", backgroundColor:"#434b56", border:"0px solid #434b56", borderRadius:"3px", color:"#dee2e6"}} 
                        className="form-control" id="moTa" rows={3} defaultValue={""} onChange={formik.handleChange} />
                        <p className="text-danger">{formik.errors.moTa}</p>
                        <div id="invalidMoTa" className="invalid-form">
                        </div>
                    </div>
                </form>
            </div>
            <div className="modal-footer">
                
                <button  className="btn btn__add_calendar" type="submit" onClick={handleSubmit}>Thm phim</button>
            </div>
        </div>
    )
}
