import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { NavLink } from 'react-router-dom'
import { deletedUser, getAllUser, getAllUserPage } from '../../../redux/Action/AdminAction';
import Pagination from "react-js-pagination";
import AddUser from '../UsersManagement/AddUser';
import UpdateUser from '../UsersManagement/UpdateUser';


export default function UsersManagement() {
    const dispatch = useDispatch();
    // Search User
    const { allUser, allUserPage } = useSelector(state => state.AdminReducer)
    // Search value input
    const [searchValue, setSearchValue] = useState('')
    // Set Update User
    const [addUpdateUser, setAddUpdateUser] = useState(true)
    // Set pagination
    const [pageNumber, setPageNumber] = useState(1);
    // Choose IDs for update
    const [userID, setUserID] = useState("");
    //Paginatiopn
    const [state, setState] = useState({
        activePage: 1
    })
    // Checked handle value
    const handleValue = (e) => {
        setSearchValue(e.target.value)
    }
    // for pagination
    let countAllUser = 0;
    for (let i = 0; i < allUser.length; i++) {
        countAllUser++
    }
    const handlePageChange = (pageNumber) => {
        // console.log(`active page is ${pageNumber}`);
        const newState = { activePage: pageNumber }
        setState(newState);
        setPageNumber(pageNumber)
        dispatch(getAllUserPage(pageNumber))
        console.log(pageNumber)
    }
    // console.log(arrUser)
    const renderSearch = () => {
        let arrItem = allUser.filter(id => (id.taiKhoan?.toLowerCase().includes(searchValue.toLowerCase()) || id.hoTen?.toLowerCase().includes(searchValue.toLowerCase())))
        console.log(arrItem)
        return arrItem.map((item, index) => {
            return <tr key={index}>
                <td>{(pageNumber - 1) * 10 + index + 1} </td>
                <td>{item?.taiKhoan}</td>
                <td>{item?.matKhau}</td>
                <td>{item?.hoTen}</td>
                <td>{item?.soDt}</td>
                <td>{item?.maLoaiNguoiDung}</td>
                {/* <td>{item?.maNhom}</td> */}
                <td>{item?.email}</td>
                <td><button data-toggle="modal" data-target="#exampleModal" className="btn__edit" onClick={() => {
                    setUserID(item)
                    setAddUpdateUser(false)
                    // console.log(item)
                }}><i class='bx bxs-edit'></i></button></td>
                <td><button className="btn__del" onClick={() => {
                    dispatch(deletedUser(item.taiKhoan))
                }} ><i class='bx bx-trash'></i></button></td>
            </tr>
        })

    }
    const renderAllUserPage = () => {
        return allUserPage.items?.map((user, index) => {
            return <tr key={index}>
                <td>{(pageNumber - 1) * 10 + index + 1}</td>
                <td>{user?.taiKhoan}</td>
                <td>{user?.matKhau}</td>
                <td>{user?.hoTen}</td>
                <td>{user?.soDt}</td>
                <td>{user?.maLoaiNguoiDung}</td>
                
                <td>{user?.email}</td>
                <td><button data-toggle="modal" data-target="#exampleModal" className="btn__edit" onClick={() => {
                    setUserID(user)
                    setAddUpdateUser(false)
                    // console.log(user)
                }} ><i class='bx bxs-edit'></i></button></td>
                <td><button className="btn__del" onClick={() => {
                    dispatch(deletedUser(user.taiKhoan))
                }}><i class='bx bx-trash'></i></button></td>
            </tr>
        })
    }
    useEffect(() => {
        dispatch(getAllUser())
        dispatch(getAllUserPage(1))
    }, [dispatch])

    return (
        <div className="users__manager">
            <div>
            
                <div className="container-fluid mt-5">
                    <div className="row">
                        <nav className="col-md-2 col-lg-2 d-md-block bg-dark sidebar__menu sidebar ">
                            <div className="sidebar-sticky pt-3">
                                <ul className="nav flex-column mt-5">
                                    <li className="nav-item">
                                        
                                        <NavLink className="nav-link nav__active" to='/admin/UsersManagement' >
                                        <i class='bx bxs-user-account'></i> Qun l ngi dng <span className="sr-only">(current)</span>
                                        </NavLink>
                                    </li>
                                    <li className="nav-item">
                                        <NavLink className="nav-link" to="/admin/MovieManagement">
                                        <i class='bx bx-film' ></i> Qun l phim
                                        </NavLink>
                                    </li>
                                    <li className="nav-item">
                                        <NavLink className="nav-link" to="/admin/CalendarMovieManagement">
                                        <i class='bx bx-calendar-star' ></i> Qun l lch chiu
                                        </NavLink>
                                    </li>

                                </ul>
                            </div>
                        </nav>
                        <main role="main" className="col-md-10 ml-sm-auto col-lg-10  mt-5">
                            
                            <h4 className="text-center title__user__manager">Qun l ngi dng</h4>

                            <div>
                                <div className="add">
                                    <button data-toggle="modal" data-target="#exampleModal" className="btn add__user" onClick={() => {
                                        setAddUpdateUser(true)
                                    }} >Thm thnh vin</button>

                                </div>
                                <div className="row my-3">
                                    <div className="col-3 ">
                                        <input
                                            className=" search__user form-control form-control-dark w-100"
                                            type="text"
                                            placeholder="Tm kim"
                                            aria-label="Search"
                                            onChange={handleValue}
                                        />
                                    </div>

                                   
                                </div>

                                <div className="table  user__manager__table">
                                    <table >
                                        <thead>
                                            <tr>
                                                <th>STT</th>
                                                <th>Ti khon</th>
                                                <th>Mt khu</th>
                                                <th>H Tn</th>
                                                <th>St</th>
                                                <th>Loi ngi dng</th>
                                                <th>Email</th>
                                                <th></th>
                                                <th></th>
                                         
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {searchValue === "" ? renderAllUserPage() : renderSearch()}
                                        </tbody>
                                    </table>


                                </div>
                            </div>
                            <div className="user__pagination mt-2">
                                        <Pagination
                                            itemClass="page-item"
                                            linkClass="page-link"
                                            // activeClass	="active__pagination"
                                            activePage={state.activePage}
                                            itemsCountPerPage={1}
                                            totalItemsCount={countAllUser / 10}
                                            pageRangeDisplayed={10}
                                            onChange={handlePageChange.bind()}
                                        />
                                    </div>
                        </main>
                    </div>
                </div>
                <div className="modal fade" id="exampleModal" tabIndex={-1} role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                    <div className="modal-dialog" role="document">
                        {addUpdateUser ? <AddUser /> : <UpdateUser user={userID} />}

                    </div>
                </div>
            </div>
        </div>
    )
}
